// src/components/Contact.js
import React from 'react';
import SocialMediaIcons from './SocialMediaIcons';
import data from './Data/data';
import './Contact.css';

const Contact = () => {
  const { companyName, companyEmail, phoneNumber, address } = data;

  return (
    <div className="contact">
      <h2>Contact Us</h2>
      <p>
        <span className="label">Company Name:</span>
        <span className="value">{`  "${companyName}"`}</span><br/>

        <span className="label">Email Address:</span>
        <span className="value">{`  "${companyEmail}"`}</span><br/>

        <span className="label">Phone Number:</span>
        <span className="value">{`  "${phoneNumber}"`}</span><br/>

        <span className="label">Address:</span>
        <span className="value">{`  "${address}"`}</span><br/>
      </p>

      <div className="social-icons">
        <SocialMediaIcons />
      </div>
    </div>
  );
};

export default Contact;
